import React from "react";
import author from "../images/home-background.jpg";
import Pdf from '../Documents/Resume Alejandro Perez Barreto.pdf';
import "./AboutMe.css";

const AboutMe = () => {

    return (
        <div className="about">

            <div id="about" className="container">

                <div className="row">
                    <h1>About Me</h1>

                    <div className="col-lg-5 col-md-6 col-sm-4 mt-3 profile__wrap ">
                        <img className="profile__img" src={author} data-holder-rendered="true" alt="" />
                    </div>

                    <div className="col-lg-5 col-md-6 col-sm-4">
                        <div className="about__component">
                            <p className="mb-4 about__description">
                                I’m a teamwork-oriented and driven aspiring Software Engineer. I’m very familiar with
                                networking and troubleshooting. Currently evolving and polishing my front-end development skills. A
                                job in web development would align with my projects and interests.
                            </p>
                            <p className="mb-4 about__position">Current Position: <span className="about__span">IT Support Technician - Farmacia Central Drug, Puerto Rico</span>
                            </p>
                            <h5 className="mb-3 py-2 education__hfive">Education</h5>
                            <div className="education__container">

                                <div className="card" style={{ "width": "200px", "height": "150px" }}>
                                    <a className="card__links" href="https://www.barry.edu/en" target="_blank" rel="noreferrer">
                                        <div className="card-body">
                                            <h6 className="card-title">Barry University</h6>
                                            <p className="card-subtitle mb-2 text-muted">December 2020</p>
                                            <p className="card-text">-B.S. in Computer Science</p>
                                            <p className="card-text">-Minor in Mathematics</p>
                                        </div>
                                    </a>
                                </div>

                                <div className="card" style={{ "width": "200px", "height": "150px" }}>
                                    <a className="card__links" href="https://csa.edu.pr/" target="_blank" rel="noreferrer">
                                        <div className="card-body">
                                            <h6 className="card-title">Colegio San Antonio</h6>
                                            <p className="card-subtitle mb-2 text-muted">May 2016</p>
                                            <p className="card-text">-Highschool Degree</p>
                                        </div>
                                    </a>
                                </div>

                            </div>
                            <div className="about__button">
                                <a className="education__button" href={Pdf} target="_blank" rel="noreferrer">View CV </a>

                            </div>
                        </div>
                    </div>
                </div>


            </div>




        </div >

    )
}

export default AboutMe
