import { Link } from "react-scroll";
import React, { useState, useEffect } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBars } from "@fortawesome/free-solid-svg-icons";
import "./Navbar.css";

const Navbar = () => {
    const [colorChange, setColorChange] = useState(false);
    const changeBackground = () => {
        window.scrollY > 66 ? setColorChange(true) : setColorChange(false);
    };

    useEffect(() => {
        window.addEventListener("scroll", changeBackground);
        return () => {
            window.removeEventListener("scroll", changeBackground);
        };
    }, []);

    return (
        <nav className={colorChange ? "navbar colorChange navbar-expand-lg navbar-light justify-content-start fixed-top" : "navbar navbar-expand-lg navbar-light fixed-top"}>
            <div className="container-fluid navbar__container">
                <a className="navbar-brand" href="#"></a>
                <button className="navbar-toggler md-auto" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="true" aria-label="Toggle navigation">
                    <FontAwesomeIcon icon={faBars} style={{ color: "#1d1d1d" }} />
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <Link smooth={true} to="home" duration={500} className="nav-link" href="#">Home</Link>
                        </li>
                        <li className="nav-item">
                            <Link smooth={true} to="about" duration={500} offset={-90} className="nav-link" href="#">About Me</Link>
                        </li>
                        <li className="nav-item">
                            <Link smooth={true} to="skills" duration={500} offset={-110} className="nav-link" href="#">Skills</Link>
                        </li>

                        <li className="nav-item">
                            <Link smooth={true} to="projects" duration={500} offset={-90} className="nav-link" href="#">Projects</Link>
                        </li>
                        <li className="nav-item">
                            <Link smooth={true} to="experience" duration={500} offset={-90} className="nav-link" href="#">Experience</Link>
                        </li>

                        <li className="nav-item">
                            <Link smooth={true} to="contactme" duration={500} offset={-90} className="nav-link" href="#">Contact</Link>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>
    )
}

export default Navbar
