import React from "react";
import "./Footer.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGithub, faLinkedin } from "@fortawesome/free-brands-svg-icons";


const Footer = () => {
        return (
                <div id="footer" className="footer">
                        <div className="container">
                                <div className="row">
                                        <div className="col-md-4 col-xs-4 text-center">
                                                <small className="text-center text-muted">perez.alejandro1221@gmail.com</small>
                                        </div>
                                        <div className="col-md-4 col-xs-4">
                                                <p className="text-center">
                                                        &copy; {new Date().getFullYear()}&nbsp;<span className="footer-lastname">Perez</span> | All Rights Reserved</p>
                                        </div>
                                        <div className="col-md-4 col-xs-4">
                                                <ul className="nav justify-content-center list-unstyled d-flex">
                                                        <li className="mx-2"><a className="text-muted" href="https://www.linkedin.com/in/alejandro-perez-655616202" target="_blank" rel="noreferrer"><FontAwesomeIcon icon={faLinkedin} /></a></li>
                                                        <li className="mx-2"><a className="text-muted" href="https://github.com/alejandro1perez" target="_blank" rel="noreferrer"><FontAwesomeIcon icon={faGithub} /></a></li>
                                                </ul>
                                        </div>
                                </div>

                        </div>

                </div>

        )

}


export default Footer
