import React, { useState, useRef } from "react";
import emailjs from '@emailjs/browser';
import { useForm } from "react-hook-form";
import "./ContactMe.css";
import ReCAPTCHA from "react-google-recaptcha";

const ContactMe = () => {
    const [successMesssage, setSuccessMessage] = useState("");
    const { register, handleSubmit, formState: { errors } } = useForm();
    const [validCaptcha, setValidCaptcha] = useState(null);
    const [validUser, setValidUser] = useState(false);

    const captcha = useRef(null);


    const serviceID = "service_g110yfw";
    const templateID = "template_ID";
    const userID = "user_vvVUpBKSeUsjisKhuzeK7";

    const onSubmit = (data, r) => {
        if (captcha.current.getValue()) {
            console.log("user is not a robot");
            setValidUser(true);
            setValidCaptcha(true);
            sendEmail(
                serviceID,
                templateID,
                {
                    email: data.email,
                    subject: data.subject,
                    description: data.description
                },
                userID
            )
            r.target.reset();
        } else {
            console.log("prove to me that you are a human");
            setValidUser(false);
            setValidCaptcha(false);
        }




    }
    const sendEmail = (serviceID, templateID, variables, userID) => {
        emailjs.send(serviceID, templateID, variables, userID)
            .then(() => {
                setSuccessMessage("Form Sent! I'll contact you as soon as possible.");
            }).catch(err => console.error('Oops something went wrong. My email is perez.alejandro1221@gmail.com '));

    }

    const onChange = () => {
        if (captcha.current.getValue()) {
            console.log("user is not a robot");
            setValidCaptcha(true);
        }
    }
    return (
        <div id="contactme" className="contacts">

            <div className="container">
                <div className="text-center mb-3">
                    <h1>contact me</h1>
                    <p>Feel free to contact me anytime by filling out this form.</p>
                    {validUser &&
                        <span className="success-message">{successMesssage}</span>
                    }
                </div>
                {!validUser &&
                    <form onSubmit={handleSubmit(onSubmit)}>
                        <div className="row d-flex justify-content-center align-items-center">
                            <div className="col-md-6 col-xs-12">
                                {/*EMAIL INPUT*/}
                                <div className="text-center">
                                    <input
                                        type="email"
                                        className="form-control"
                                        placeholder="Email"
                                        name="email"
                                        {...register("email", {
                                            required: "Please provide your email address",
                                            pattern: {
                                                value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                                                message: "invalid Email"
                                            }
                                        })
                                        }
                                    />
                                    <div className="line"></div>
                                </div>
                                <span className="error-message">
                                    {errors.message && errors.message.message}
                                </span>
                                {/*SUBJECT INPUT*/}
                                <div className="text-center">
                                    <input
                                        type="text"
                                        className="form-control"
                                        placeholder="Subject"
                                        name="subject"
                                        {...register("subject", {
                                            required: "Required",
                                        })
                                        }
                                    />
                                    <div className="line"></div>
                                </div>
                                <span className="error-message">
                                    {errors.message && errors.message.message}
                                </span>
                            </div>

                            {/*DESCRIPTION*/}
                            <div className="text-center">
                                <textarea
                                    id="description"
                                    type="text"
                                    className="form-control"
                                    placeholder="Description"
                                    name="description"
                                    {...register("description", {
                                        required: "Please provide description",
                                    })
                                    }
                                ></textarea>
                                <div className="line"></div>
                            </div>
                            <span className="error-message">
                                {errors.message && errors.message.message}
                            </span>
                            <div className="recaptcha mb-3">
                                <ReCAPTCHA
                                    ref={captcha}
                                    sitekey="6LdUFvEfAAAAALRp3faatM38gDSJN6Y6BFuMXO7e"
                                    onChange={onChange}
                                />
                            </div>
                            {validCaptcha === false && <div className="error-captcha">Please fill out captcha</div>}
                            <button className="contact__button" type="submit">
                                Contact Me
                            </button>

                        </div>
                    </form>
                }
            </div>
        </div>
    )
}

export default ContactMe
